
import Layout from '@/layout'
export default [
  {
    path: '/',
    component: Layout,
    redirect: '/poetry',
    children: [
      {
        path: '/home',
        name: 'Home',
        component: resolve => require(['@/views/article/index'], resolve),
        meta: {
          title: '首页',
          keywords: '人文资料，生活常识',
          description: '人文资料，生活常识',
          requiresAuth: false
        }
      }
    ]
  },
  {
    path: '/view',
    component: Layout,
    children: [
      {
        path: '/view/:id',
        name: 'MaterialView',
        component: resolve => require(['@/views/article/view'], resolve),
        meta: {
          title: '查看',
          iconfont: '&#xe641;',
          requiresAuth: false
        }
      }
    ]
  },
  {
    path: '/poetry',
    component: Layout,
    children: [
      {
        path: '/poetry',
        name: 'Poetry',
        component: resolve => require(['@/views/article/poetry'], resolve),
        meta: {
          title: '诗词',
          keywords: '唐诗宋词',
          description: '唐诗宋词',
          iconfont: '&#xe641;',
          requiresAuth: false
        }
      }
    ]
  }
]
