import Layout from '@/layout'

export default [
  /**
  {
    path: '/not-auth',
    name: '无权限',
    component: resolve => require(['@/views/error/not-auth'], resolve),
    meta: {
      title: '无权限'
    }
  },
  */
  {
    path: '*',
    component: Layout,
    children: [
      {
        path: '',
        name: '404',
        component: resolve => require(['@/views/error/not-found'], resolve),
        meta: {
          title: '页面不存在',
          requiresAuth: false
        }
      }
    ]
  }
]
