<template>
  <el-container>
    <el-header style="height: auto; padding: 0">
      <el-menu
        :default-active="activeMenu"
        router
        class="menu-container"
        mode="horizontal"
        background-color="#3c8dbc"
        text-color="#ffffff"
        active-text-color="#ffd04b"
        menu-trigger="click"
      >
        <el-menu-item index="/home">
          首页
        </el-menu-item>
        <el-menu-item index="/poetry">
          诗词
        </el-menu-item>
        <el-submenu index="more">
          <template slot="title">更多</template>
          <el-menu-item><el-link :underline="false" href="https://github.com/seffeng" style="color:#ffffff" target="_blank">Github</el-link></el-menu-item>
          <el-divider />
          <el-menu-item><el-link :underline="false" href="http://www.1kmi.com" style="color:#ffffff" target="_blank">1kmi.com</el-link></el-menu-item>
          <el-menu-item><el-link :underline="false" href="https://www.wuhuawu.com" style="color:#ffffff" target="_blank">wuhuawu.com</el-link></el-menu-item>
        </el-submenu>
        <el-menu-item v-show="canSearch()">
          <el-input v-model="searchForm.title" class="input-with-select" clearable placeholder="请输入内容" @keyup.enter.native="keySearch">
            <el-button slot="append" icon="el-icon-search" @click="headerSearch" />
          </el-input>
        </el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <router-view ref="childPageView" />
    </el-main>
    <el-footer><div class="footer fl">©{{ year() }} <el-link href="https://weibo.com/seffeng" target="_blank" style="color: #f9f0f0">@seffeng</el-link></div></el-footer>
    <Backtop />
  </el-container>
</template>

<script>
import { debug, parseTime, inArray } from '@/utils'
import Backtop from '@/components/Backtop.vue'

export default {
  components: {
    Backtop
  },
  data() {
    return {
      searchForm: {
        title: ''
      },
      activeMenu: '/home'
    }
  },
  created() {
    this.activeMenu = this.$route.path
  },
  methods: {
    canSearch() {
      return inArray(this.$route.name, ['Home', 'Poetry'])
    },
    headerSearch() {
      debug(this.searchForm.title)
      if (this.$refs.childPageView.headerSearch !== undefined) {
        this.$refs.childPageView.headerSearch(this.searchForm.title)
      }
    },
    keySearch(e) {
      this.headerSearch()
    },
    year() {
      const now = new Date()
      return parseTime(now, '{y}')
    }
  }
}
</script>

<style lang="scss" scoped>
.el-header, .el-footer {
  background-color: #3c8dbc;
  color: #333;
  text-align: center;
  line-height: 60px;

  .footer {
    font-size: 14px;
    color: #fff;
  }
}

.el-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
}

.el-input {
  margin: 10px;
  width: 230px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.el-menu > .el-divider {
  margin: 8px 0;
}
</style>
